import {
  Button,
  FormControl,
  MenuItem,
  Select,
  TextField,
} from "@mui/material";
import React, { useState } from "react";
import BlueButton from "../../common/BlueButton/BlueButton";
import style from "./SelectedServicePage.module.css";
import { Stack } from "@mui/system";
import { DesktopDatePicker, LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import PopupSuccessTemplate from "../../common/PopupSuccessTemplate/PopupSuccessTemplate";
import ButtonBack from "../../common/ButtonBack/ButtonBack";

const SelectedServicePage = ({ id, medicalCares, closePage }) => {
  const [isReservSuccess, setIsReservSuccess] = useState(false);

  return (
    <div>
      {isReservSuccess && (
        <PopupSuccessTemplate
          handleClick={closePage}
          text="   "
          subtext="true"
        />
      )}
      <ButtonBack handleClick={closePage}/>
      {medicalCares.map((d) => {
        if (d.id === id) {
          const cost = d.cost === 0 ? "" : `${d.cost} `;
          return (
            <div key={d.id}>
              <h1 className={style.title}>{d.text}</h1>
              <div className={style.selectInputs}>
                <FormControl sx={{ width: { xs: "344px", md: "255px" } }}>
                  <span className={style.selectInputs__title}></span>
                  <Select displayEmpty IconComponent={ExpandMoreIcon}>
                    <MenuItem> .A.</MenuItem>
                  </Select>
                </FormControl>

                <FormControl
                  sx={{
                    width: { xs: "154px", md: "255px" },
                    mt: { xs: "10px", md: "0" },
                  }}
                >
                  <LocalizationProvider
                    dateAdapter={AdapterDayjs}
                    sx={{ width: "100px" }}
                  >
                    <Stack>
                      <span className={style.selectInputs__title}></span>
                      <DesktopDatePicker
                        IconComponent={ExpandMoreIcon}
                        inputFormat="MM.DD.YYYY"
                        renderInput={(params) => <TextField {...params} />}
                      />
                    </Stack>
                  </LocalizationProvider>
                </FormControl>
                <FormControl
                  IconComponent={ExpandMoreIcon}
                  sx={{
                    width: { xs: "154px", md: "160px" },
                    mt: { xs: "10px", md: "0" },
                  }}
                >
                  <span className={style.selectInputs__title}></span>
                  <TextField
                    IconComponent={ExpandMoreIcon}
                    id="time"
                    type="time"
                    defaultValue="07:30"
                    InputLabelProps={{
                      shrink: true,
                    }}
                    sx={{ width: 160 }}
                  />
                </FormControl>
              </div>
              <div className={style.info}>
                <ul className={style.info__first}>
                  <li> :</li>
                  <li> :</li>
                  <li> :</li>
                  <li>  :</li>
                </ul>
                <ul className={style.info__second}>
                  <li>{d.doctorProfile}</li>
                  <li>{cost}</li>
                  <li>{d.place}</li>
                  <li>{d.preparation}</li>
                </ul>
              </div>
              <div className={style.submitButton}>
                <BlueButton
                  handleClick={() => setIsReservSuccess(true)}
                  text="  "
                  width="100%"
                />
              </div>
            </div>
          );
        }
      })}
    </div>
  );
};

export default SelectedServicePage;

